import { Component, Input, Output } from '@angular/core';
import { OnDestroy, AfterContentInit } from '@angular/core';
import { EventEmitter, ContentChildren, QueryList } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';

import { TdStepComponent } from './step.component';

export interface IStepChangeEvent {
  newStep: TdStepComponent;
  prevStep: TdStepComponent;
}

export enum StepMode {
  Vertical = 'vertical',
  Horizontal = 'horizontal',
}

@Component({
  selector: 'td-steps',
  styles: [`.td-line-wrapper,
.td-step{
  position:relative; }

.td-steps-header{
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  display:-webkit-box;
  display:-ms-flexbox;
  display:flex;
  -webkit-box-orient:horizontal;
  -webkit-box-direction:normal;
      -ms-flex-direction:row;
          flex-direction:row; }

.td-line-wrapper{
  width:24px;
  min-height:1px; }

.td-horizontal-line{
  border-bottom-width:1px;
  border-bottom-style:solid;
  height:1px;
  position:relative;
  top:36px;
  min-width:15px;
  -webkit-box-flex:1;
      -ms-flex:1;
          flex:1;
  -webkit-box-sizing:border-box;
          box-sizing:border-box; }
  ::ng-deep :not([dir='rtl']) .td-horizontal-line{
    left:-6px;
    right:-3px; }
  ::ng-deep [dir='rtl'] .td-horizontal-line{
    left:-3px;
    right:-6px; }

.td-vertical-line{
  position:absolute;
  bottom:-16px;
  top:-16px;
  border-left-width:1px;
  border-left-style:solid; }
  ::ng-deep :not([dir='rtl']) .td-vertical-line{
    left:20px;
    right:auto; }
  ::ng-deep [dir='rtl'] .td-vertical-line{
    left:auto;
    right:20px; }
`],
  template: `<div *ngIf="isHorizontal()" class="td-steps-header">
  <ng-template let-step let-index="index" let-last="last" ngFor [ngForOf]="steps">
    <td-step-header class="td-step-horizontal-header"
                    (keydown.enter)="step.open()"
                    [number]="index + 1"
                    [active]="step.active"
                    [disableRipple]="step.disableRipple"
                    [disabled]="step.disabled" 
                    [state]="step.state"
                    (click)="step.open()">
      <ng-template td-step-header-label [cdkPortalHost]="step.stepLabel"></ng-template>
      <ng-template td-step-header-label [ngIf]="!step.stepLabel">{{step.label}}</ng-template>
      <ng-template td-step-header-sublabel [ngIf]="true">{{step.sublabel | truncate:30}}</ng-template>
    </td-step-header>
    <span *ngIf="!last" class="td-horizontal-line"></span>
  </ng-template>
</div>
<div *ngFor="let step of steps; let index = index; let last = last" class="td-step">
  <td-step-header class="td-step-vertical-header"
                  (keydown.enter)="step.toggle()"
                  [number]="index + 1"
                  [active]="step.active" 
                  [disabled]="step.disabled"
                  [disableRipple]="step.disableRipple"
                  [state]="step.state"
                  (click)="step.toggle()"
                  *ngIf="isVertical()">
    <ng-template td-step-header-label [cdkPortalHost]="step.stepLabel"></ng-template>
    <ng-template td-step-header-label [ngIf]="!step.stepLabel">{{step.label}}</ng-template>
    <ng-template td-step-header-sublabel [ngIf]="true">{{step.sublabel}}</ng-template>
  </td-step-header>
  <ng-template [ngIf]="isVertical() || step.active || (!areStepsActive() && prevStep === step)">
    <td-step-body [active]="step.active" [state]="step.state">
      <div *ngIf="isVertical()" class="td-line-wrapper">
        <div *ngIf="!last" class="td-vertical-line"></div>
      </div>
      <ng-template td-step-body-content [cdkPortalHost]="step.stepContent"></ng-template>
      <ng-template td-step-body-actions [cdkPortalHost]="step.stepActions"></ng-template>
      <ng-template td-step-body-summary [cdkPortalHost]="step.stepSummary"></ng-template>
    </td-step-body>
  </ng-template>
</div>
`,
})
export class TdStepsComponent implements OnDestroy, AfterContentInit {

  private _subcriptions: Subscription[];
  private _mode: StepMode = StepMode.Vertical;
  private _steps: QueryList<TdStepComponent>;

  @ContentChildren(TdStepComponent)
  set stepsContent(steps: QueryList<TdStepComponent>) {
    if (steps) {
      this._steps = steps;
      this._registerSteps();
    }
  }

  get steps(): TdStepComponent[] {
    return this._steps.toArray();
  }
  prevStep: TdStepComponent;

  /**
   * mode?: StepMode or ["vertical" | "horizontal"]
   * Defines if the mode of the [TdStepsComponent].  Defaults to [StepMode.Vertical | "vertical"]
   */
  @Input('mode')
  set mode(mode: StepMode) {
    switch (mode) {
      case StepMode.Horizontal:
        this._mode = StepMode.Horizontal;
        break;
      default:
        this._mode = StepMode.Vertical;
    }
  }
  get mode(): StepMode {
    return this._mode;
  }

  /**
   * stepChange?: function
   * Method to be executed when [onStepChange] event is emitted.
   * Emits an [IStepChangeEvent] implemented object.
   */
  @Output('stepChange') onStepChange: EventEmitter<IStepChangeEvent> = new EventEmitter<IStepChangeEvent>();

  /**
   * Executed after content is initialized, loops through any [TdStepComponent] children elements,
   * assigns them a number and subscribes as an observer to their [onActivated] event.
   */
  ngAfterContentInit(): void {
    this._registerSteps();
  }

  /**
   * Unsubscribes from [TdStepComponent] children elements when component is destroyed.
   */
  ngOnDestroy(): void {
    this._deregisterSteps();
  }

  /**
   * Returns 'true' if [mode] equals to [StepMode.Horizontal | 'horizontal'], else 'false'.
   */
  isHorizontal(): boolean {
    return this._mode === StepMode.Horizontal;
  }

  /**
   * Returns 'true' if [mode] equals to [StepMode.Vertical | 'vertical'], else 'false'.
   */
  isVertical(): boolean {
    return this._mode === StepMode.Vertical;
  }

  areStepsActive(): boolean {
    return this._steps.filter((step: TdStepComponent) => {
      return step.active;
    }).length > 0;
  }

  /**
   * Wraps previous and new [TdStepComponent] numbers in an object that implements [IStepChangeEvent]
   * and emits [onStepChange] event.
   */
  private _onStepSelection(step: TdStepComponent): void {
    if (this.prevStep !== step) {
      let prevStep: TdStepComponent = this.prevStep;
      this.prevStep = step;
      let event: IStepChangeEvent = {
        newStep: step,
        prevStep: prevStep,
      };
      this._deactivateAllBut(step);
      this.onStepChange.emit(event);
    }
  }

  /**
   * Loops through [TdStepComponent] children elements and deactivates them ignoring the one passed as an argument.
   */
  private _deactivateAllBut(activeStep: TdStepComponent): void {
    this._steps.filter((step: TdStepComponent) => step !== activeStep)
    .forEach((step: TdStepComponent) => {
      step.active = false;
    });
  }

  private _registerSteps(): void {
    this._subcriptions = [];
    this._steps.toArray().forEach((step: TdStepComponent) => {
      let subscription: Subscription = step.onActivated.asObservable().subscribe(() => {
        this._onStepSelection(step);
      });
      this._subcriptions.push(subscription);
    });
  }

  private _deregisterSteps(): void {
    if (this._subcriptions) {
      this._subcriptions.forEach((subs: Subscription) => {
        subs.unsubscribe();
      });
      this._subcriptions = undefined;
    }
  }
}
